hange=\"handleMakerScreen\"\n                required\n            >\n                <option value=\"\" disabled selected>Select</option>\n                <option v-for=\"screen in props.modelsOfModule.screens\" :id=\"screen.ID\">\n                    {{ screen.name }}\n                </option>\n            </select>\n\n            <hr width=\"1\" size=\"500\" class=\"line first-line\" />\n\n            <p class=\"combo-label first-approver-text\">1st Approver Screen</p>\n            <select\n                class=\"combo first-approver-combo\"\n                id=\"first-approver-screen\"\n                :style=\"'color:' + (false ? ' black' : '#A1A1A1')\"\n                v-model=\"firstApproverScreen\"\n                @change=\"handleCheckerScreen($event, 1)\"\n                required\n            >\n                <option value=\"\" disabled selected>Select</option>\n                <option v-for=\"screen in props.modelsOfModule.screens\" :id=\"screen.ID\">\n                    {{ screen.name }}\n                </option>\n            </select>\n\n            <div v-if=\"approverScreenCounter > 1\">\n                <p class=\"combo-label second-approver-text\">2nd Approver Screen</p>\n                <select\n                    class=\"combo second-approver-combo\"\n                    id=\"second-approver-screen\"\n                    :style=\"'color:' + (false ? ' black' : '#A1A1A1')\"\n                    v-model=\"secondApproverScreen\"\n                    @change=\"handleCheckerScreen($event, 2)\"\n                    required\n                >\n                    <option value=\"\" disabled selected>Select</option>\n                    <option v-for=\"screen in props.modelsOfModule.screens\" :id=\"screen.ID\">\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <i\n                    v-if=\"approverScreenCounter == 2\"\n                    class=\"mdi mdi-delete-outline delete-icon second-delete-icon\"\n                    @click=\"decreaseApproverCounter()\"\n                >\n                </i>\n            </div>\n\n            <div v-if=\"approverScreenCounter > 2\">\n                <p class=\"combo-label third-approver-text\">3rd Approver Screen</p>\n                <select\n                    class=\"combo third-approver-combo\"\n                    id=\"third-approver-screen\"\n                    :style=\"'color:' + (false ? ' black' : '#A1A1A1')\"\n                    v-model=\"thirdApproverScreen\"\n                    @change=\"handleCheckerScreen($event, 3)\"\n                    required\n                >\n                    <option value=\"\" disabled selected>Select</option>\n                    <option v-for=\"screen in props.modelsOfModule.screens\" :id=\"screen.ID\">\n                        {{ screen.name }}\n                    </option>\n                </select>\n                <i\n                    v-if=\"approverScreenCounter == 3\"\n                    class=\"mdi mdi-delete-outline delete-icon third-delete-icon\"\n                    @click=\"decreaseApproverCounter()\"\n                >\n                </i>\n            </div>\n\n            <div v-if=\"approverScreenCounter > 3\">\n                <p class=\"combo-label fourth-approver-text\">4th Approver Screen</p>\n                <select\n                    class=\"combo fourth-approver-combo\"\n                    :style=\"'color:' + (false ? ' black' : '#A1A1A1')\"\n                    id=\"fourth-approver-screen\"\n                    v-model=\"fourthApproverScreen\"\n                    @change=\"handleCheckerScreen($event, 4)\"\n                    required\n                >\n                    <option value=\"\" disabled selected>Select</option>\n                    <option v-for=\"screen in props.modelsOfModule.screens\" :id=\"screen.ID\">\n                        {{ screen.name }}\n                    </option>\n                </select>