___CSS_LOADER_EXPORT___.push([module.id, "\n.process-editor[data-v-2077d6cb] {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 1000px) / 2);\n    margin-top: 26px;\n    border: 1px solid #d5d5d5;\n    border-radius: 4px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/editors/processEditor/processEditor.vue"],"names":[],"mappings":";AAuPA;IACI,aAAa;IACb,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,sCAAsC;IACtC,gBAAgB;IAChB,yBAAyB;IACzB,kBAAkB;AACtB","sourcesContent":["<script setup lang=\"ts\">\nimport { IProcessEditorProps } from \"../../../../application/objects/editors/processEditorImpl\";\nimport { ref } from \"vue\";\nimport {\n    SINGLE_APPROVAL,\n    DOUBLE_APPROVAL,\n    TRIPLE_APPROVAL,\n    QUADRUPLE_APPROVAL,\n    QUINTUPLE_APPROVAL,\n    extraProcessConfigVariables,\n} from \"../../../../domain/objects/editors/processEditor/constants/processTemplates\";\nimport ProcessDefinitionTab from \"./approvalWizards/tabs/processDefinitionTab.vue\";\nimport EditProcessVariablesTab from \"./approvalWizards/tabs/editProcessVariablesTab.vue\";\nimport SingleApprovalWizard from \"./approvalWizards/singleApprovalWizard.vue\";\nimport DoubleApprovalWizard from \"./approvalWizards/doubleApprovalWizard.vue\";\nimport TripleApprovalWizard from \"./approvalWizards/tripleApprovalWizard.vue\";\nimport QuadrupleApprovalWizard from \"./approvalWizards/quadrupleApprovalWizard.vue\";\nimport QuintupleApprovalWizard from \"./approvalWizards/quintupleApprovalWizard.vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { ICreateModel } from \"../../../../domain/useCase/ICreateModel\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\n\nimport { IModule } from \"../../../../domain/model/models\";\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IProcessEditorUtil } from \"../../../../domain/useCase/IProcessEditorUtil\";\nimport { IModelsOfModule } from \"../../../../domain/presentation/editors/IProcessEditorModel\";\nimport { extractProcessVariables } from \"@stechquick/plateau-process-designer\";\nimport ProcessEditorHeader from \"./components/processEditorHeader.vue\";\nimport ProcessEditorFooter from \"./components/processEditorFooter.vue\";\nimport ScreenSelectionTab from \"./tabs/ScreenSelectionTab.vue\";\nimport ServiceCallConfigurationTab from \"./tabs/serviceCallConfigurationTab.vue\";\nimport AdvancedSettings from \"./tabs/advancedSettings.vue\";\n\ninterface IProps {\n    functions: IProcessEditorProps[\"functions\"];\n    callbacks: IProcessEditorProps[\"callbacks\"];\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst props = defineProps<IProps>();\nconst processDefinitionTabKey = ref(\"\");\nconst disableFinishButton = ref(false);\nconst isNextActive = ref(true);\n\nconst processModel = ref(processEditorUtil.getEmptyProcessWizardModel(\"_\"));\nconst processEditorState = ref(processEditorUtil.getEmptyProcessEditorState());\nconst currentModule = ref({} as IModule);\nconst modelsOfModule = ref({\n    screens: [],\n    bpmns: [],\n} as IModelsOfModule);\n\nprops.functions.getModel = async () => {\n    return { model: processModel.value, state: processEditorState.value };\n};\n\nprops.functions.setModel = async (model, module, state) => {\n    currentModule.value = module;\n    modelsOfModule.value = processEditorUtil.getModelsOfModule(module);\n    processDefinitionTabKey.value = CryptoHelper.CreateGuid();\n    processModel.value = model;\n    processEditorState.value = state;\n};\n\nasync function handleNext() {\n    const stepper = processEditorState.value.stepper;\n    const selectedExistingBpmnFile = processModel.value.selectedExistingBpmnFile;\n    if (stepper.isTabValid) {\n        if (!processModel.value.createNewBpmnFile) {\n            isNextActive.value = false;\n            const existingBpmnVariableCache = processEditorState.value.existingBpmnVariableCache;\n            const bpmnVariableFromCache = existingBpmnVariableCache[processModel.value.selectedExistingBpmnFile.ID];\n            if (bpmnVariableFromCache) {\n                processModel.value.selectedExistingBpmnFile.variables = bpmnVariableFromCache;\n            }\n            const bpmnModel = await props.callbacks.getBpmnModel(selectedExistingBpmnFile.ID);\n            const extractedProcessVariables = await extractProcessVariables(bpmnModel);\n            const editableVariables = processEditorUtil\n                .extractEditableProcessVariables(extractedProcessVariables, [\n                    \"initiator\",\n                    \"formJson\",\n                    \"userId\",\n                    \"tenantId\",\n                    \"isChange\",\n                    \"refreshAfterSendToApproval\",\n                ])\n                .concat(extraProcessConfigVariables);\n\n            editableVariables.forEach((editableVariable: any) => {\n                if (!processModel.value.selectedExistingBpmnFile.variables.some((variable) => variable.key === editableVariable.source)) {\n                    processModel.value.selectedExistingBpmnFile.variables.push({\n                        key: editableVariable.source,\n                        type: \"STRING\",\n                        value: processEditorUtil.handleProcessVariables(\n                            processModel.value,\n                            editableVariable.source,\n                            modelsOfModule.value,\n                            \"process_name\"\n                        ),\n                        isDeletable: false,\n                    });\n                }\n            });\n            processModel.value.selectedExistingBpmnFile.variables = processModel.value.selectedExistingBpmnFile.variables.filter((variable) =>\n                editableVariables.some((editableVariable) => editableVariable.source == variable.key)\n            );\n            isNextActive.value = true;\n        }\n        stepper.currentStep++;\n        stepper.isTabValid = false;\n        props.callbacks.modified({ state: true });\n    } else stepper.currentStep++;\n}\n\nfunction handleBack() {\n    processEditorState.value.stepper!.currentStep--;\n    processEditorState.value.stepper!.isTabValid = false;\n    props.callbacks.modified({ state: true });\n}\n\nfunction handleFinish() {\n    const bpmnFile = processEditorUtil.generateBpmnFile(processModel.value, processEditorState.value.creatingProcessFileName);\n    executor.execute(\n        async () => {\n            const createdItem = await executor.executeUseCase<ICreateModel>(ICreateModel, {\n                name: processEditorState.value.creatingProcessFileName,\n                parentObjectId: currentModule.value.ID,\n                modelType: \"bpmn\",\n                modelBody: [{ key: \"bpmn\", model: bpmnFile }],\n                modelAdditionals: {},\n            });\n\n            if (processModel.value.createNewBpmnFile) {\n                processModel.value.selectedExistingBpmnFile.variables = [];\n            }\n\n            processModel.value.createNewBpmnFile = false;\n\n            processModel.value.selectedExistingBpmnFile.ID = createdItem.ID;\n            modelsOfModule.value.bpmns.push(createdItem);\n            processEditorState.value.stepper.currentStep = 0;\n            disableFinishButton.value = true;\n            studio.openItem(createdItem, { fromHistory: false, noFocus: true });\n        },\n        { loading: true }\n    );\n    props.callbacks.modified({ state: true });\n}\n</script>\n<template>\n    <div class=\"process-editor\" v-if=\"true\">\n        <ProcessEditorHeader :state=\"processEditorState\"/>\n        <ScreenSelectionTab\n            v-if=\"processEditorState.stepper.currentStep === 1\"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <ServiceCallConfigurationTab v-if=\"processEditorState.stepper.currentStep === 2\" />\n        <AdvancedSettings v-if=\"processEditorState.stepper.currentStep === 3\" />\n        <ProcessEditorFooter :handle-next=\"handleNext\" :handle-back=\"handleBack\" :state=\"processEditorState\" />\n    </div>\n    <div class=\"process-editor\" v-if=\"false\">\n        <ProcessDefinitionTab\n            v-if=\"processEditorState.stepper.currentStep === 0\"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n            :enable-finish-button=\"() => (disableFinishButton = false)\"\n            :is-next-active=\"isNextActive\"\n            :key=\"processDefinitionTabKey\"\n        />\n        <EditProcessVariablesTab\n            v-if=\"!processModel.createNewBpmnFile && processEditorState.stepper.currentStep === 1\"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <SingleApprovalWizard\n            v-if=\"\n                processModel.processType === SINGLE_APPROVAL.value && processEditorState.stepper.currentStep !== 0 && processModel.createNewBpmnFile\n            \"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <DoubleApprovalWizard\n            v-if=\"\n                processModel.processType === DOUBLE_APPROVAL.value && processEditorState.stepper.currentStep !== 0 && processModel.createNewBpmnFile\n            \"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <TripleApprovalWizard\n            v-if=\"\n                processModel.processType === TRIPLE_APPROVAL.value && processEditorState.stepper.currentStep !== 0 && processModel.createNewBpmnFile\n            \"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <QuadrupleApprovalWizard\n            v-if=\"\n                processModel.processType === QUADRUPLE_APPROVAL.value &&\n                processEditorState.stepper.currentStep !== 0 &&\n                processModel.createNewBpmnFile\n            \"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <QuintupleApprovalWizard\n            v-if=\"\n                processModel.processType === QUINTUPLE_APPROVAL.value &&\n                processEditorState.stepper.currentStep !== 0 &&\n                processModel.createNewBpmnFile\n            \"\n            :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\"\n            :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\"\n        />\n        <div>\n            <button style=\"margin-bottom: 5%\" :disabled=\"!processEditorState.stepper.currentStep\" @click=\"handleBack\">Back</button>\n            <button\n                v-if=\"!processEditorState.stepper.isApproveTab && !(!processModel.createNewBpmnFile && processEditorState.stepper.currentStep === 1)\"\n                @click=\"handleNext\"\n                :disabled=\"!processEditorState.stepper.isTabValid || !isNextActive\"\n            >\n                Next\n            </button>\n            <button v-if=\"processEditorState.stepper.isApproveTab\" @click=\"handleFinish\" :disabled=\"disableFinishButton\">Finish</button>\n        </div>\n    </div>\n</template>\n<style scoped>\n.process-editor {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 1000px) / 2);\n    margin-top: 26px;\n    border: 1px solid #d5d5d5;\n    border-radius: 4px;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
